import React from 'react';
import MovieModal from './components/movieModal'
import './App.css';
import noImage from './images/no-image.png'
import loader from './images/media2.gif'
import { Button, Modal } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../node_modules/font-awesome/css/font-awesome.min.css';
import $ from 'jquery';
const BASE_URL = 'http://192.168.1.8:9125';
//import { post } from '../../server/app';

let playPath = "";
class App extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            movieArray: [],
            movieListClone: [],
            moviePoster: "",
            movieTitle: "",
            myMovie: "",
            modalHidden: true,
            currentMovie: {},
            show: false,
            mainDivHidden: false,
            movieFilePath: "",
            videoPlayerHidden: true,
            movieDetails: "",
            updateDBResp: "",
            category: false,
            categories: [
                'English',
                'Hindi',
                'Punjabi'
            ],
            dirFolders: [],
            movieByNameArray: [],
            filePath: ""
        }
    }

    componentDidMount = async () => {
        // this.startHTTPServer();
        this.showLoader();
        //this.listFiles();
        // this.updateList()

        // this.queryMovie();

    }

    startHTTPServer = async () => {
        try {
            const resp = await fetch(`${BASE_URL}/startHttpServer`);
            const data = await resp.json();
            console.log(data);
        } catch (error) {
            console.log(error)
        }
    }

    showLoader = () => {
        $("#loader").toggle();
    }

    hideLoader = () => {
        $('#loader').hide();
    }

    listFiles = async (dirName) => {
        this.showLoader();
        this.setState({ filePath: dirName });
        const { movieArray, dirFolders, movieListClone } = this.state;
        let movieFolders = [];
        let newObj = {};
        let nonExist = [];

        await this.queryMovie();
        console.log(movieArray.length)
        if (movieArray.length > 0) {
            await this.fetchDirFolders(dirName)
                .then(async () => {
                    await Promise.all(dirFolders.map(itm => {
                        // for (let i = 0; i < res1.length; i++) {
                        movieFolders.push({ "movie": itm.movie })
                        // }
                        //console.log(movieArray.length + ">>>>>>>" + movieFolders.length)
                    }))
                    return movieArray;
                })
                .then(async detail => {
                    console.log(detail)
                    await Promise.all(movieFolders.map(async itm => {
                        let movie = itm.movie;
                        let mName = movie.slice(0, movie.length - 7);
                        let mYear = movie.slice(movie.length - 4);
                        await this.queryMovieByName(mName, mYear)
                            .then((resp) => {
                                if (resp.length == 0) {
                                    nonExist.push({ "movieName": itm.movieTitle, "year": itm.movieYear });
                                }
                                else {
                                    movieListClone.push(resp)
                                }
                            })
                    }))
                    console.log(movieListClone)
                    this.hideLoader();

                })
                .then(async () => {
                    if (nonExist.length > 0) {
                        await Promise.all(nonExist.map(async j => {
                            console.log(j)
                            let data = {};
                            data.name = j.movieName;
                            data.year = j.year;
                            await this.fetchDetailsAPI(data)
                                .then(async () => {
                                    const { movieDetails } = this.state;
                                    console.log(movieDetails);
                                    if (movieDetails) {
                                        if (movieDetails !== 'error') {
                                            await this.updateMovieDb(movieDetails);
                                        } else {
                                            await this.queryMovie();
                                        }
                                    }
                                })
                        }))
                            .then(async () => {
                                const { updateDBResp } = this.state;
                                if (updateDBResp === true) {
                                    console.log(updateDBResp);
                                    await this.queryMovie();
                                }
                            })
                    }
                })
            // .then(async () => {
            //     await fetch(`${BASE_URL}/endDbSession`)
            //         .then(res => res.json())
            //         .then(res => {
            //             console.log(res)
            //         })
            // })
        }



    }


    fetchDetailsAPI = async (details) => {
        // await fetch(`https://data-imdb1.p.rapidapi.com/titles/search/title/afsar?info=mini_info&limit=10&page=1&titleType=movie&year=2018`, {
        await fetch(`https://data-imdb1.p.rapidapi.com/titles/search/title/${details.name}?info=mini_info&limit=10&page=1&titleType=movie&year=${details.year}`, {
            "method": "GET",
            "headers": {
                "x-rapidapi-host": "data-imdb1.p.rapidapi.com",
                "x-rapidapi-key": "2df8caced5msh14040f60a51c6f7p12f27ajsn5f6c17ccb7d6"
            }
        })
            .then(response => response.json())
            .then((data) => {
                console.log(data);
                let resp = data.results.find((items) => items.titleText.text === details.name);
                this.setState({ movieDetails: resp })
                console.log(resp)
            })
            .catch(err => {
                this.setState({ movieDetails: 'error' })
            })
    }

    updateMovieDb = async (movieDetails) => {

        let theHeaders = new Headers();
        theHeaders.append("Content-Type", "application/json");

        let rawData = JSON.stringify({
            "details": movieDetails
        })
        let params = {
            method: 'POST',
            headers: theHeaders,
            body: rawData,

        }
        try {
            const resp = await fetch(`${BASE_URL}/updateMovieDb`, params);
            const data = await resp.json();
            this.setState({ updateDBResp: data.acknowledged })

        } catch (error) {
            console.log(error)
        }

    }


    fetchDirFolders = async (dirName) => {
        const { dirFolders } = this.state;
        let theHeaders = new Headers();
        theHeaders.append("Content-Type", "application/json");

        let rawData = JSON.stringify({
            "dir": dirName
        })
        let params = {
            method: 'POST',
            headers: theHeaders,
            body: rawData,

        }
        try {
            const resp = await fetch(`${BASE_URL}/library/`, params);
            const data = await resp.json();
            await Promise.all(data.map(itm => {
                dirFolders.push(itm)
            }));

        } catch (error) {
            console.log(error)
        }

    }

    queryMovieByName = async (movieName) => {
        const { movieByNameArray } = this.state;
        let theHeaders = new Headers();
        theHeaders.append("Content-Type", "application/json");

        let rawData = JSON.stringify({
            "title": movieName
        })
        let params = {
            method: 'POST',
            headers: theHeaders,
            body: rawData,

        }
        try {
            const resp = await fetch(`${BASE_URL}/queryMovieByName/`, params);
            const data = await resp.json();
            return data;
            // console.log(data);
            // await Promise.all(data.map(itm => {
            //     let releaseDate = itm.releaseDate.slice(itm.releaseDate.length - 4);                
            //     movieByNameArray.push({movieTitle: itm.title, movieYear : releaseDate})
            // }));

        } catch (error) {
            console.log(error)
        }
    }

    queryMovie = async () => {
        const { movieArray } = this.state;
        try {
            const response = await fetch(`${BASE_URL}/queryMovie`);
            const data = await response.json();
            await Promise.all(data.map(item => {
                movieArray.push(item)
            }))

        } catch (error) {
            console.log(error)
        }
    }

    updateList = () => {
        //this.showLoader();
        setTimeout(() => {
            const { movieArray } = this.state;
            this.setState({ movieListClone: movieArray })
        }, 2000);


    }

    myfunc = async (detail) => {
        const { filePath } = this.state;
        this.setState({ modalHidden: false })
        this.setState({ currentMovie: detail, show: true })
        let dirName = detail[0].title;
        let releaseYear = (detail[0].releaseDate).slice(detail[0].releaseDate.length - 4);
        let myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");

        let raw = JSON.stringify({
            "dirName": dirName,
            "releaseYear": releaseYear,
            "path": filePath
        });
        let requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: raw,
            redirect: 'follow'
        };
        try {
            const resp = await fetch(`${BASE_URL}/filepath`, requestOptions);
            const data = await resp.json();
            console.log(data);
            if (data != "not supported") {
                let fullMoviePath = `http://localhost:8080/${dirName} - ${releaseYear}/${data.file}`
                let newPath = encodeURI(fullMoviePath.trim());
                this.setState({ movieFilePath: newPath })
            } else if (data == "not supported") {
                this.setState({ movieFilePath: "unsupported" })
            }

        } catch (error) {
            console.log(error);
        }
    }

    playMovie = () => {
        console.log(playPath)
        setTimeout(() => {

            this.setState({ videoPlayerHidden: false })
        }, 1000);
    }

    openMovieModal = (e) => {
        let movie = e.target.value;
    }

    handleClose = () => {
        this.setState({ show: false })
        this.setState({ movieFilePath: "" })
        // this.setState({ mainDivHidden: false })
    }

    handleCategory = async (e) => {
        if (e == 'English') {
            await this.listFiles('English-Movies');
            this.setState({ category: true });
        }
        else if (e == 'Hindi') {
            await this.listFiles('Hindi-Movies');
            this.setState({ category: true });
        }
        else if (e == 'Punjabi') {
            await this.listFiles('Punjabi-Movies');
            this.setState({ category: true });
        }
        //this.setState({category: true})
    }

    goBack = () => {
        this.setState({ category: false });
        this.setState({movieListClone:[],movieArray:[], dirFolders:[]})
    }


    render() {
        return (
            <div className='mainDiv'>
                <div className='headingDiv'>
                    <button className='btn1' onClick={this.goBack}><i className="fa fa-arrow-circle-left"></i></button>
                    <h1 className='heading'  onClick={this.goBack}>Homeflix</h1>
                </div>
                {this.state.category !== true ?
                    <div>
                        <div className='categories' onClick={this.testClick}>Categories</div>
                        <div className='categoryParent'>
                            {this.state.categories.map((cate, idx) => {
                                return (
                                    <li key={idx} className='category' onClick={() => this.handleCategory(cate)}>{cate}</li>
                                )
                            })}
                        </div>
                    </div> :

                    <div hidden={this.state.mainDivHidden}>
                        {/* <div className='movieList' ></div> */}
                        {/* <button onClick={this.updateList}>click me</button> */}
                        <div className='movieList'>
                            {this.state.movieListClone.map((item, idx) => {
                                let releaseYear = item[0].releaseDate.slice(item[0].releaseDate.length - 4);
                                let movieName = item[0].title + " (" + releaseYear + ")";
                                return (
                                    <li key={idx} onClick={() => this.myfunc(item)}>
                                        <img src={item[0].imageUrl} width='150px'></img>
                                        <h2>{movieName}</h2>
                                    </li>
                                    // <button key={idx} onClick={(idx) => this.openMovieModal(idx)} value={item.item} style={{ marginTop: "10px", display: "flex" }}>{item.item}</button>
                                )
                            })}
                        </div>
                        {this.state.movieFilePath.length > 0 ?
                            <Modal size='lg' centered show={this.state.show} onHide={this.handleClose} backdrop="static"
                                keyboard={false}>
                                <Modal.Header closeButton>
                                    {/* {this.props.details.titleText.text} */}
                                </Modal.Header>
                                <Modal.Body>
                                    {this.state.movieFilePath != "unsupported" ?
                                        <video width='100%' height='100%' controls>
                                            <source src={this.state.movieFilePath} type='video/mp4'></source>
                                        </video>
                                        : <div className='unsupportedDiv'>
                                            <h2>Video format not supported</h2>
                                        </div>
                                    }
                                    {/* <ReactPlayer url={this.state.playMovie} width="100%" height="100%" controls={true} type="video/x-matroska" /> */}

                                </Modal.Body>
                                <Modal.Footer>
                                    <Button variant="secondary" onClick={this.handleClose}>
                                        Close
                                    </Button>
                                    {/* <Button variant="primary" onClick={this.playMovie}>
                                Play Movie
                            </Button> */}
                                </Modal.Footer>
                            </Modal>
                            : null}
                    </div>
                }
                <div className='loaderParent'>
                    <div id="loader" >
                        <img src={loader} />
                    </div>
                </div>
            </div>
        )
    }

}

export default App;